.modal-content {
  color: #212529;
}

.modal, .modal-open {
  padding: 0px !important;
}

.modal-header .close {
    margin: -1rem -1rem -1rem;
}

.modal-title {
  margin: auto;
}

.modal-body {
  text-align: center;
  font-size: small;
  font-weight: normal;
}

.modal-footer {
  justify-content: center;
  border-top: none;
}
